.toggle {
  cursor: pointer;
  width: 40px;
  height: 22px;
  background-color: #8c8c8c;
  border: 1px;
  border-radius: 30px;
  align-items: center;
  display: flex;
  position: relative;
}

.switched {
  cursor: pointer;
  width: 40px;
  height: 22px;
  background-color: #fff;
  border: 1px;
  border-radius: 30px;
  align-items: center;
  display: flex;
  position: relative;
}

.toggle:after {
  content: "";
  width: 20px;
  height: 20px;
  background-color: #ff6b08;
  border-radius: 50%;
  transition: all .5s;
  position: absolute;
  top: 1px;
  left: 1px;
}

.switched:after {
  content: "";
  width: 20px;
  height: 20px;
  background-color: #ff6b08;
  border-radius: 50%;
  transition: all .5s;
  position: absolute;
  top: 1px;
  left: 19px;
}

